import React, { useEffect, useState } from "react";
import { db } from "./firebase";
import {
  collection,
  query,
  orderBy,
  onSnapshot,
  doc,
  updateDoc,
  writeBatch,
} from "firebase/firestore";
import { useNavigate } from "react-router-dom";

const MealPlanChanges = () => {
  const [changes, setChanges] = useState([]);
  const [clearing, setClearing] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    const q = query(collection(db, "mealPlanChanges"), orderBy("timestamp", "desc"));

    const unsubscribe = onSnapshot(q, (querySnapshot) => {
      const changesData = [];
      querySnapshot.forEach((doc) => {
        changesData.push({ id: doc.id, ...doc.data() });
      });
      setChanges(changesData);
    });

    return () => unsubscribe();
  }, []);

  const acknowledgeChange = async (id) => {
    const docRef = doc(db, "mealPlanChanges", id);
    await updateDoc(docRef, { acknowledged: true });
  };

  const clearOldNotifications = async () => {
    if (!window.confirm("Are you sure you want to clear all acknowledged notifications?")) return;

    setClearing(true);
    const batch = writeBatch(db);

    changes
      .filter((c) => c.acknowledged)
      .forEach((c) => {
        const docRef = doc(db, "mealPlanChanges", c.id);
        batch.delete(docRef);
      });

    try {
      await batch.commit();
    } catch (err) {
      alert("Failed to clear notifications: " + err.message);
    }
    setClearing(false);
  };

  // Pronouns map
  const pronouns = {
    Vaishu: { possessive: "her", subject: "she", object: "her" },
    default: { possessive: "his", subject: "he", object: "him" },
  };

  const formatChangeMessage = ({ user, day, previousValue, newValue, timestamp }) => {
    const date = timestamp && timestamp.toDate ? timestamp.toDate() : new Date();
    const dayDateStr = date.toLocaleDateString("en-US", { month: "short", day: "numeric", year: "numeric" });
    const timeStr = date.toLocaleTimeString("en-US", { hour: "2-digit", minute: "2-digit", hour12: true });

    const p = pronouns[user] || pronouns.default;

    return `${user} has changed ${p.possessive} ${day} (${dayDateStr}) meal plan from "${previousValue}" to "${newValue}" at ${timeStr}`;
  };

  return (
    <div style={{ padding: "20px", maxWidth: "700px", margin: "auto" }}>
      <h2>Meal Plan Change Notifications</h2>
      <div style={{ marginBottom: 16 }}>
        <button onClick={() => navigate("/")} style={{ cursor: "pointer" }}>
          Back to Meal Tracker
        </button>
        <button
          onClick={clearOldNotifications}
          disabled={clearing}
          style={{ marginLeft: 16, cursor: clearing ? "wait" : "pointer" }}
        >
          Clear Acknowledged Notifications
        </button>
      </div>
      {changes.length === 0 ? (
        <p>No notifications</p>
      ) : (
        <ul style={{ listStyle: "none", padding: 0 }}>
          {changes.map((change) => {
            const isAcknowledged = change.acknowledged === true || change.acknowledged === "true";

            return (
              <li
                key={change.id}
                style={{
                  marginBottom: 8,
                  padding: "8px 12px",
                  borderRadius: 5,
                  backgroundColor: isAcknowledged ? "#f0f0f0" : "#ffffff",
                  border: "1px solid #ccc",
                  fontSize: 14,
                  whiteSpace: "normal", // allow wrapping
                  overflow: "visible", // allow button to show fully
                  textOverflow: "unset",
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "center",
                }}
                title={formatChangeMessage(change)}
              >
                <span style={{ flex: 1, paddingRight: 12 }}>
                  {formatChangeMessage(change)}
                </span>
                {!isAcknowledged && (
                  <button
                    onClick={() => acknowledgeChange(change.id)}
                    style={{
                      cursor: "pointer",
                      fontSize: 16,
                      padding: "4px 8px",
                      minWidth: 30,
                      height: 30,
                      lineHeight: 1,
                      borderRadius: 4,
                      border: "1px solid #007bff",
                      backgroundColor: "#e7f1ff",
                      color: "#007bff",
                    }}
                    aria-label="Acknowledge change"
                  >
                    
                  </button>
                )}
              </li>
            );
          })}
        </ul>
      )}
    </div>
  );
};

export default MealPlanChanges;







