import React, { useEffect, useState } from "react";
import { db } from "./firebase";
import { doc, getDoc } from "firebase/firestore";
import { useUser } from "./UserContext";
import { useNavigate } from "react-router-dom";

import { days, getWeekDates, getWeekId, CountdownToReset } from "./utils";

const SummaryPage = () => {
  const { user, logout } = useUser();
  const [mealData, setMealData] = useState(null);
  const navigate = useNavigate();
  const weekId = getWeekId();
  const dates = getWeekDates();

  useEffect(() => {
    const fetchData = async () => {
      const docRef = doc(db, "weeks", weekId);
      const docSnap = await getDoc(docRef);

      if (docSnap.exists()) {
        setMealData(docSnap.data().users?.[user] || {});
      } else {
        setMealData({});
      }
    };
    fetchData();
  }, [user, weekId]);

  if (!mealData) return <p style={{ textAlign: "center", marginTop: "50px", fontSize: "1.2rem" }}>Loading...</p>;

  // --- Styles ---
  const containerStyle = {
    maxWidth: "900px",
    margin: "40px auto",
    padding: "25px 30px",
    fontFamily:
      "'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif",
    backgroundColor: "#fff",
    borderRadius: "14px",
    boxShadow: "0 10px 30px rgba(0,0,0,0.1)",
    color: "#333",
    userSelect: "none",
  };

  const headerStyle = {
    fontWeight: "700",
    fontSize: "2rem",
    marginBottom: "25px",
    textAlign: "center",
    color: "#4a90e2",
  };

  const buttonStyle = {
    cursor: "pointer",
    padding: "10px 24px",
    fontSize: "1rem",
    fontWeight: "600",
    borderRadius: "8px",
    border: "none",
    backgroundColor: "#4a90e2",
    color: "#fff",
    boxShadow: "0 6px 14px rgba(74,144,226,0.5)",
    transition: "background-color 0.3s ease",
  };

  const buttonGroupStyle = {
    display: "flex",
    justifyContent: "center",
    gap: "16px",
    marginBottom: "30px",
  };

  const tableStyle = {
    width: "100%",
    borderCollapse: "separate",
    borderSpacing: "0 14px",
  };

  const thStyle = {
    backgroundColor: "#4a90e2",
    color: "#fff",
    padding: "12px",
    fontWeight: "700",
    fontSize: "1rem",
    borderTopLeftRadius: "12px",
    borderTopRightRadius: "12px",
    userSelect: "none",
  };

  const tdStyle = {
    backgroundColor: "#f9f9f9",
    padding: "14px",
    textAlign: "center",
    fontWeight: "600",
    borderRadius: "8px",
    boxShadow: "inset 0 0 6px rgba(0,0,0,0.05)",
    userSelect: "none",
  };

  return (
    <div style={containerStyle}>
      <h2 style={headerStyle}>{user}'s Meal Plan Summary</h2>

      <div style={buttonGroupStyle}>
        <button
          onClick={() => navigate("/")}
          style={buttonStyle}
          onMouseEnter={(e) => (e.currentTarget.style.backgroundColor = "#3c7bd9")}
          onMouseLeave={(e) => (e.currentTarget.style.backgroundColor = "#4a90e2")}
          aria-label="Back to Edit"
        >
          Back to Edit
        </button>
        <button
          onClick={logout}
          style={{ ...buttonStyle, backgroundColor: "#e94e4e" }}
          onMouseEnter={(e) => (e.currentTarget.style.backgroundColor = "#c84141")}
          onMouseLeave={(e) => (e.currentTarget.style.backgroundColor = "#e94e4e")}
          aria-label="Logout"
        >
          Logout
        </button>
      </div>

      <table style={tableStyle} aria-label="Meal plan summary table">
        <thead>
          <tr>
            {dates.map((date, i) => (
              <th key={i} style={thStyle}>
                {days[i]} <br />
                <small style={{ fontWeight: "400", fontSize: "0.9rem" }}>
                  ({date.toLocaleDateString("en-US", { month: "short", day: "numeric" })})
                </small>
              </th>
            ))}
          </tr>
        </thead>
        <tbody>
          <tr>
            {days.map((day) => {
              const lunch = mealData?.[day]?.lunch;
              const dinner = mealData?.[day]?.dinner;
              return (
                <td key={day} style={tdStyle}>
                  {lunch && " Lunch"}
                  {lunch && dinner && <br />}
                  {dinner && " Dinner"}
                  {!lunch && !dinner && "No meal"}
                </td>
              );
            })}
          </tr>
        </tbody>
      </table>

      <div style={{ marginTop: "36px", textAlign: "center" }}>
        <CountdownToReset />
      </div>
    </div>
  );
};

export default SummaryPage;


