import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useUser } from "./UserContext";
import { db } from "./firebase";
import { doc, getDoc } from "firebase/firestore";

const users = ["Ram", "Vaishu", "Suppiah", "Komathi"];
const days = ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"];

const getWeekDates = () => {
  const today = new Date();
  const monday = new Date(today.setDate(today.getDate() - today.getDay() + 1));
  const dates = [];
  for (let i = 0; i < 7; i++) {
    const d = new Date(monday);
    d.setDate(monday.getDate() + i);
    dates.push(d);
  }
  return dates;
};

const getWeekId = () => getWeekDates()[0].toISOString().split("T")[0];

const AdminView = () => {
  const navigate = useNavigate();
  const { logout } = useUser();
  const [mealData, setMealData] = useState({});
  const [dates, setDates] = useState(getWeekDates());
  const weekId = getWeekId();

  useEffect(() => {
    const fetchMealData = async () => {
      const docRef = doc(db, "weeks", weekId);
      const docSnap = await getDoc(docRef);

      if (docSnap.exists()) {
        setMealData(docSnap.data().users || {});
      } else {
        setMealData({});
      }
    };

    fetchMealData();
    setDates(getWeekDates()); // keep dates updated
  }, [weekId]);

  const filteredUsers = users.filter((u) => u !== "Komathi");

  return (
    <div style={{ padding: "20px" }}>
      <h2>Admin View </h2>

      <div style={{ marginBottom: "15px" }}>
        <button
          onClick={() => navigate("/meal-plan-changes")}
          style={{ marginRight: "10px", padding: "8px 15px", cursor: "pointer" }}
        >
          View Meal Plan Changes
        </button>
        <button onClick={logout} style={{ padding: "8px 15px", cursor: "pointer" }}>
          Log out
        </button>
      </div>

      <div style={{ overflowX: "auto" }}>
        <table
          border="1"
          cellPadding="10"
          style={{
            marginTop: "10px",
            minWidth: "900px",
            borderCollapse: "collapse",
            tableLayout: "fixed",
          }}
        >
          <thead style={{ position: "sticky", top: 0, backgroundColor: "#f0f0f0", zIndex: 1 }}>
            <tr>
              <th style={{ width: "120px" }}>User</th>
              {dates.map((date, i) => (
                <th key={i} style={{ width: "90px" }}>
                  {days[i]} <br />
                  ({date.toLocaleDateString("en-US", { month: "short", day: "numeric" })})
                </th>
              ))}
              <th style={{ width: "120px" }}>Choice Made</th> {/* <-- added this */}
            </tr>
          </thead>
          <tbody>
            {filteredUsers.map((username) => {
              const userMeals = mealData[username] || {};

              // Check if user has any meal or 'neither' selected for any day
              const hasSelectedChoice = days.some((day) => {
                const meal = userMeals[day];
                return meal && (meal.lunch || meal.dinner || meal.neither);
              });

              return (
                <tr key={username}>
                  <td style={{ fontWeight: "bold", textAlign: "center" }}>{username}</td>
                  {days.map((day) => {
                    const lunch = userMeals[day]?.lunch;
                    const dinner = userMeals[day]?.dinner;
                    const neither = userMeals[day]?.neither;

                    return (
                      <td key={day} style={{ textAlign: "center", whiteSpace: "nowrap" }}>
                        {lunch && " Lunch"}
                        {lunch && dinner && <br />}
                        {dinner && " Dinner"}
                        {(lunch || dinner) && neither && <br />}
                        {neither && " Neither"}
                        {!lunch && !dinner && !neither && "No meal"}
                      </td>
                    );
                  })}
                  <td
                    style={{
                      color: hasSelectedChoice ? "green" : "red",
                      fontWeight: "bold",
                      textAlign: "center",
                    }}
                  >
                    {hasSelectedChoice ? "Yes" : "No"}
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default AdminView;





