import React, { useEffect, useState } from "react";
import { db } from "./firebase";
import {
  doc,
  getDoc,
  setDoc,
  updateDoc,
  addDoc,
  collection,
  serverTimestamp,
} from "firebase/firestore";
import { useUser } from "./UserContext";
import { useNavigate } from "react-router-dom";

import { days, getWeekDates, getWeekId, CountdownToReset } from "./utils";

const users = ["Ram", "Vaishu", "Suppiah", "Komathi"];

const MealTracker = () => {
  const [mealData, setMealData] = useState({});
  const [error, setError] = useState("");
  const [showNotification, setShowNotification] = useState(false);

  const weekId = getWeekId();
  const dates = getWeekDates();
  const { user, logout } = useUser();
  const navigate = useNavigate();

  useEffect(() => {
    const fetchData = async () => {
      const docRef = doc(db, "weeks", weekId);
      const docSnap = await getDoc(docRef);

      if (docSnap.exists()) {
        setMealData(docSnap.data().users || {});
      } else {
        const initialData = {};
        users.forEach((u) => {
          initialData[u] = {};
          days.forEach((day) => {
            initialData[u][day] = { lunch: false, dinner: false, neither: false };
          });
        });
        await setDoc(docRef, { users: initialData });
        setMealData(initialData);
      }
    };

    fetchData();
  }, [weekId]);

  // --- Styles ---
  const containerStyle = {
    maxWidth: "1000px",
    margin: "30px auto",
    padding: "20px",
    fontFamily:
      "'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif",
    color: user === "Komathi" ? "#fff" : "#333",
    backgroundColor: user === "Komathi" ? "#8B0000" : "#f9f9f9",
    borderRadius: "12px",
    boxShadow:
      user === "Komathi"
        ? "0 8px 24px rgba(139,0,0,0.8)"
        : "0 4px 12px rgba(0,0,0,0.1)",
  };

  const headerStyle = {
    marginBottom: "20px",
    fontWeight: "700",
    fontSize: "2.25rem",
    userSelect: "none",
    textShadow: user === "Komathi" ? "2px 2px 6px rgba(0,0,0,0.6)" : "none",
  };

  const buttonStyle = {
    cursor: "pointer",
    padding: "12px 25px",
    fontSize: "1rem",
    fontWeight: "600",
    borderRadius: "8px",
    border: "none",
    transition: "all 0.25s ease",
    marginBottom: "25px",
    ...(user === "Komathi"
      ? {
          backgroundColor: "#B22222",
          color: "#fff",
          boxShadow: "0 6px 12px rgba(178,34,34,0.6)",
        }
      : {
          backgroundColor: "#4a90e2",
          color: "#fff",
          boxShadow: "0 6px 12px rgba(74,144,226,0.4)",
        }),
  };

  const tableStyle = {
    width: "100%",
    borderCollapse: "separate",
    borderSpacing: "0 12px",
    userSelect: "none",
  };

  const thStyle = {
    backgroundColor: user === "Komathi" ? "#B22222" : "#4a90e2",
    color: "#fff",
    padding: "12px",
    borderTopLeftRadius: "12px",
    borderTopRightRadius: "12px",
    fontWeight: "700",
    fontSize: "1rem",
  };

  const tdStyle = {
    backgroundColor: user === "Komathi" ? "#7f0000" : "#fff",
    padding: "12px",
    textAlign: "center",
    borderBottom: user === "Komathi" ? "none" : "1px solid #ddd",
    borderRadius: "8px",
    boxShadow: user === "Komathi" ? "inset 0 0 10px rgba(0,0,0,0.3)" : "none",
  };

  const inputCheckboxStyle = {
    cursor: "pointer",
    width: "18px",
    height: "18px",
    marginRight: "6px",
    accentColor: user === "Komathi" ? "#ff6347" : "#4a90e2",
  };

  const errorStyle = {
    color: "#ff4d4f",
    fontWeight: "700",
    marginTop: "10px",
    fontSize: "1rem",
    userSelect: "none",
  };

  // --- Admin View ---
  if (user === "Komathi") {
    return (
      <div style={containerStyle}>
        <h2 style={headerStyle}>Welcome, {user}! (Admin View) </h2>
        <button
          onClick={logout}
          style={{ ...buttonStyle, width: "100px", marginBottom: "20px" }}
          onMouseEnter={(e) => (e.currentTarget.style.backgroundColor = "#a71d1d")}
          onMouseLeave={(e) => (e.currentTarget.style.backgroundColor = "#B22222")}
        >
          Log out
        </button>

        <table style={tableStyle}>
          <thead>
            <tr>
              <th style={{ ...thStyle, borderTopLeftRadius: "12px" }}>User</th>
              {dates.map((date, i) => (
                <th key={i} style={thStyle}>
                  <div>
                    {days[i]} <br />
                    <small style={{ fontWeight: "400" }}>
                      ({date.toLocaleDateString("en-US", { month: "short", day: "numeric" })})
                    </small>
                  </div>
                </th>
              ))}
            </tr>
          </thead>

          <tbody>
            {users
              .filter((u) => u !== "Komathi")
              .map((u) => (
                <tr key={u} style={{ userSelect: "none" }}>
                  <td
                    style={{
                      ...tdStyle,
                      fontWeight: "700",
                      textAlign: "left",
                      paddingLeft: "15px",
                    }}
                  >
                    {u}
                  </td>
                  {days.map((day) => {
                    const dayData = mealData?.[u]?.[day] || {};
                    const { lunch, dinner, neither } = dayData;
                    return (
                      <td key={day} style={tdStyle}>
                        {lunch && " Lunch"}
                        {lunch && dinner && <br />}
                        {dinner && " Dinner"}
                        {(lunch || dinner) && neither && <br />}
                        {neither && " Neither"}
                        {!lunch && !dinner && !neither && "No meal"}
                      </td>
                    );
                  })}
                </tr>
              ))}
          </tbody>
        </table>
        <CountdownToReset />
      </div>
    );
  }

  // --- User View ---
  return (
    <div style={containerStyle}>
      <h2 style={headerStyle}>Welcome, {user}! </h2>
      <button
        onClick={logout}
        style={buttonStyle}
        onMouseEnter={(e) => (e.currentTarget.style.backgroundColor = "#3c7bd9")}
        onMouseLeave={(e) => (e.currentTarget.style.backgroundColor = "#4a90e2")}
      >
        Log out
      </button>

      <table style={tableStyle}>
        <thead>
          <tr>
            <th style={{ ...thStyle, borderTopLeftRadius: "12px" }}>User</th>
            {dates.map((date, i) => (
              <th key={i} style={thStyle}>
                <div>
                  {days[i]} <br />
                  <small style={{ fontWeight: "400" }}>
                    ({date.toLocaleDateString("en-US", { month: "short", day: "numeric" })})
                  </small>
                </div>
              </th>
            ))}
          </tr>
        </thead>

        <tbody>
          <tr style={{ userSelect: "none" }}>
            <td
              style={{ ...tdStyle, fontWeight: "700", textAlign: "left", paddingLeft: "15px" }}
            >
              {user}
            </td>
            {days.map((day) => {
              const dayData =
                mealData?.[user]?.[day] || { lunch: false, dinner: false, neither: false };
              return (
                <td key={day} style={tdStyle}>
                  <label
                    style={{
                      cursor: dayData.neither ? "not-allowed" : "pointer",
                      display: "block",
                      marginBottom: "6px",
                      userSelect: "none",
                    }}
                  >
                    <input
                      type="checkbox"
                      style={inputCheckboxStyle}
                      checked={dayData.lunch}
                      disabled={dayData.neither}
                      onChange={() => handleCheckboxChange(user, day, "lunch")}
                    />
                    Lunch
                  </label>
                  <label
                    style={{
                      cursor: dayData.neither ? "not-allowed" : "pointer",
                      display: "block",
                      marginBottom: "6px",
                      userSelect: "none",
                    }}
                  >
                    <input
                      type="checkbox"
                      style={inputCheckboxStyle}
                      checked={dayData.dinner}
                      disabled={dayData.neither}
                      onChange={() => handleCheckboxChange(user, day, "dinner")}
                    />
                    Dinner
                  </label>
                  <label
                    style={{
                      cursor: "pointer",
                      display: "block",
                      userSelect: "none",
                    }}
                  >
                    <input
                      type="checkbox"
                      style={inputCheckboxStyle}
                      checked={dayData.neither}
                      onChange={() => handleCheckboxChange(user, day, "neither")}
                    />
                    Neither
                  </label>
                </td>
              );
            })}
          </tr>
        </tbody>
      </table>

      <button
        onClick={handleSubmit}
        style={{
          marginTop: "30px",
          padding: "14px 30px",
          fontSize: "18px",
          cursor: "pointer",
          borderRadius: "8px",
          backgroundColor: "#4a90e2",
          color: "#fff",
          border: "none",
          fontWeight: "700",
          boxShadow: "0 6px 12px rgba(74,144,226,0.5)",
          transition: "all 0.3s ease",
        }}
        onMouseEnter={(e) => (e.currentTarget.style.backgroundColor = "#3c7bd9")}
        onMouseLeave={(e) => (e.currentTarget.style.backgroundColor = "#4a90e2")}
      >
        Submit Changes
      </button>

      {error && <div style={errorStyle}>{error}</div>}

      {showNotification && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            width: "100vw",
            height: "100vh",
            backgroundColor: "rgba(0,0,0,0.6)",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            zIndex: 10000,
          }}
        >
          <div
            style={{
              backgroundColor: "#fff",
              padding: "35px 40px",
              borderRadius: "16px",
              boxShadow: "0 8px 28px rgba(0,0,0,0.3)",
              textAlign: "center",
              maxWidth: "320px",
              fontWeight: "600",
              fontSize: "1.1rem",
              userSelect: "none",
            }}
          >
            <p style={{ marginBottom: "25px", color: "#4BB543", fontWeight: "800" }}>
              Changes saved successfully! 
            </p>
            <button
              onClick={() => {
                setShowNotification(false);
                navigate("/summary");
              }}
              style={{
                padding: "12px 28px",
                fontSize: "1rem",
                cursor: "pointer",
                borderRadius: "10px",
                backgroundColor: "#4BB543",
                border: "none",
                color: "#fff",
                fontWeight: "700",
                transition: "all 0.3s ease",
              }}
              onMouseEnter={(e) => (e.currentTarget.style.backgroundColor = "#399934")}
              onMouseLeave={(e) => (e.currentTarget.style.backgroundColor = "#4BB543")}
            >
              Go to Summary
            </button>
          </div>
        </div>
      )}

      <CountdownToReset />
    </div>
  );

  // Handle checkbox changes for current user
  async function handleCheckboxChange(user, day, mealType) {
    setError("");
    const currentData = mealData[user]?.[day] || {
      lunch: false,
      dinner: false,
      neither: false,
    };

    let updatedDayData = { ...currentData };

    if (mealType === "neither") {
      // Toggle 'neither'
      updatedDayData.neither = !currentData.neither;
      if (updatedDayData.neither) {
        updatedDayData.lunch = false;
        updatedDayData.dinner = false;
      }
    } else {
      // Toggle lunch/dinner, disable neither
      updatedDayData[mealType] = !currentData[mealType];
      if (updatedDayData[mealType]) {
        updatedDayData.neither = false;
      }
    }

    // Validation: must have at least one meal or neither
    if (
      !updatedDayData.lunch &&
      !updatedDayData.dinner &&
      !updatedDayData.neither
    ) {
      setError("Please select at least one meal or 'Neither'.");
      return;
    }

    setMealData((prev) => ({
      ...prev,
      [user]: {
        ...prev[user],
        [day]: updatedDayData,
      },
    }));
  }

  async function handleSubmit() {
    setError("");
    try {
      const docRef = doc(db, "weeks", weekId);
      await updateDoc(docRef, {
        users: mealData,
        updatedAt: serverTimestamp(),
      });
      setShowNotification(true);
    } catch (err) {
      setError("Failed to save changes. Please try again.");
    }
  }
};

export default MealTracker;
















