// src/utils.js
import React, { useEffect, useState } from "react";

export const days = ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"];

export const getWeekDates = () => {
  const now = new Date();

  // Calculate Sunday 4 PM this week
  const dayOfWeek = now.getDay(); // Sunday=0
  const sunday4pm = new Date(now);
  sunday4pm.setHours(16, 0, 0, 0);

  if (dayOfWeek === 0) {
    sunday4pm.setDate(now.getDate()); // today is Sunday
  } else {
    sunday4pm.setDate(now.getDate() - dayOfWeek); // previous Sunday
  }

  let monday;

  if (now > sunday4pm) {
    // After Sunday 4 PM  next Monday
    monday = new Date(sunday4pm);
    monday.setDate(sunday4pm.getDate() + 1);
    monday.setHours(0, 0, 0, 0);
  } else {
    // Before Sunday 4 PM  current week Monday
    monday = new Date(now);
    monday.setDate(now.getDate() - ((dayOfWeek + 6) % 7));
    monday.setHours(0, 0, 0, 0);
  }

  const dates = [];
  for (let i = 0; i < 7; i++) {
    const d = new Date(monday);
    d.setDate(monday.getDate() + i);
    dates.push(d);
  }
  return dates;
};

export const getWeekId = () => getWeekDates()[0].toISOString().split("T")[0];

// Countdown timer to next Sunday 4 PM
export const CountdownToReset = () => {
  const calculateNextReset = () => {
    const now = new Date();
    const dayOfWeek = now.getDay(); // Sunday=0

    const nextSunday4pm = new Date(now);
    nextSunday4pm.setHours(16, 0, 0, 0);
    nextSunday4pm.setDate(now.getDate() + ((7 - dayOfWeek) % 7));

    if (now >= nextSunday4pm) {
      nextSunday4pm.setDate(nextSunday4pm.getDate() + 7);
    }

    return nextSunday4pm;
  };

  const [timeLeft, setTimeLeft] = useState(() => {
    const diff = calculateNextReset() - new Date();
    return diff > 0 ? diff : 0;
  });

  useEffect(() => {
    const interval = setInterval(() => {
      const diff = calculateNextReset() - new Date();
      setTimeLeft(diff > 0 ? diff : 0);
    }, 1000);

    return () => clearInterval(interval);
  }, []);

  if (timeLeft <= 0) {
    return <p>The app is resetting the week now!</p>;
  }

  const daysLeft = Math.floor(timeLeft / (1000 * 60 * 60 * 24));
  const hoursLeft = Math.floor((timeLeft / (1000 * 60 * 60)) % 24);
  const minutesLeft = Math.floor((timeLeft / (1000 * 60)) % 60);
  const secondsLeft = Math.floor((timeLeft / 1000) % 60);

  return (
    <p style={{ marginTop: "10px", fontStyle: "italic", color: "#555" }}>
      The app will reset the week at Sunday 4:00 PM (in{" "}
      {daysLeft} day{daysLeft !== 1 ? "s" : ""}, {hoursLeft} hour{hoursLeft !== 1 ? "s" : ""},{" "}
      {minutesLeft} minute{minutesLeft !== 1 ? "s" : ""} and {secondsLeft} second{secondsLeft !== 1 ? "s" : ""}
      ).
    </p>
  );
};
