// src/WelcomePage.js
import React from "react";
import { useUser } from "./UserContext";

const users = ["Ram", "Vaishu", "Suppiah", "Komathi"];

const WelcomePage = () => {
  const { login } = useUser();

  return (
    <div
      style={{
        height: "100vh",
        background:
          "radial-gradient(circle at top left, #ffefba, #ffffff), linear-gradient(135deg, #f06, #4a90e2)",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
        fontFamily:
          "'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif",
        color: "#333",
        padding: "0 20px",
      }}
    >
      <h1
        style={{
          fontSize: "4rem",
          marginBottom: "10px",
          textShadow: "2px 2px 8px rgba(0,0,0,0.15)",
          userSelect: "none",
        }}
      >
         LunchLogger
      </h1>
      <p
        style={{
          fontSize: "1.5rem",
          marginBottom: "40px",
          fontWeight: "600",
          color: "#444",
          userSelect: "none",
        }}
      >
        Choose your account to begin
      </p>
      <div
        style={{
          display: "flex",
          gap: "20px",
          flexWrap: "wrap",
          justifyContent: "center",
          maxWidth: "600px",
          width: "100%",
        }}
      >
        {users.map((user) => (
          <button
            key={user}
            onClick={() => login(user)}
            aria-label={`Log in as ${user}`}
            style={{
              flex: "1 1 120px",
              background:
                "linear-gradient(135deg, #ff7e5f, #feb47b)",
              border: "none",
              borderRadius: "12px",
              padding: "15px 0",
              fontSize: "1.25rem",
              fontWeight: "700",
              color: "#fff",
              boxShadow: "0 8px 15px rgba(255,126,95,0.3)",
              cursor: "pointer",
              transition: "all 0.3s ease",
              userSelect: "none",
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.transform = "scale(1.05)";
              e.currentTarget.style.boxShadow =
                "0 12px 20px rgba(255,126,95,0.5)";
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.transform = "scale(1)";
              e.currentTarget.style.boxShadow =
                "0 8px 15px rgba(255,126,95,0.3)";
            }}
          >
            {user}
          </button>
        ))}
      </div>
      <footer
        style={{
          position: "absolute",
          bottom: "20px",
          fontSize: "0.85rem",
          color: "rgba(255,255,255,0.7)",
          userSelect: "none",
        }}
      >
        Powered by  LunchLogger Team
      </footer>
    </div>
  );
};

export default WelcomePage;

